import React from "react";
import Icon from "../Icon";
import classes from "./Event.module.css";
import natures from "../../../Assets/Strings/Natures";
import AbilityNames from "./AbilityNames";
import moveNames from "../../../Assets/Strings/Moves";
import ItemDrops from "./ItemDrops";

const Detail = props => {
    const moves = [props.data.Move0, props.data.Move1, props.data.Move2, props.data.Move3].reduce((prev, cur) => {
        if(cur === 0) {
            return prev;
        }
        return prev.concat(cur);
    }, []).map((v, idx) => {
        return <li key={idx}>{moveNames[v]}</li>;
    })
    return (
        <div className={classes.Entry}>
            <div className={classes.Center}><Icon details={props.data} /></div>
            <p><strong>Rarity: </strong></p>
            <ul>
                {props.data.Probabilities.map((i, idx) => {
                    return i > 0 ? <li key={idx}>{"☆".repeat(idx+1)}: {i}%</li> : null
                })}
            </ul>
            <p><strong>Level:</strong> {props.data.Level}</p>
            <p><strong>Dynamax Boost:</strong> {props.data.DynamaxBoost}x</p>
            <p><strong>Dynamax Level:</strong> {props.data.DynamaxLevel}</p>
            <p><strong>Dynamax Shield Level:</strong> {props.data.Field_1E}</p>
            <p><strong>Guaranteed Flawless IVs:</strong> {props.data.FlawlessIVs}</p>
            {props.data.Field_13 & 4 ? <p><strong>Catchable:</strong> No</p> : null}
            {props.data.ShinyForced ? <p><strong>Shiny:</strong> {props.data.ShinyForced === 1 ? "Locked" : "Forced Square"}</p> : null}
            <p><strong>Abilities:</strong></p><AbilityNames species={props.data.Species} altForm={props.data.AltForm} ability={props.data.Ability} />
            {props.data.Nature !== 25 ? <p><strong>Nature:</strong> {natures[props.data.Nature]}</p> : null}
            <p><strong>Moves: </strong></p><ul>{moves}</ul>
            {<ItemDrops level={props.data.MaxRank} bonus={props.data.BonusTableID} drops={props.data.DropTableID} bonusTables={props.bonus} dropTables={props.drops} />}
        </div>
        );
};

export default Detail;