import React from "react";
import classes from "./Layout.module.css";
import { NavLink } from "react-router-dom";

const Layout = props => {
        return (
            <>
            <header>
                <nav className={classes.Nav}>
                    <ul>
                        <li><NavLink to="/" exact activeClassName={classes.Active} className={classes.Link}>Seed Checker</NavLink></li>
                        <li><NavLink to="/event/" activeClassName={classes.Active} className={classes.Link}>Event List</NavLink></li>
                    </ul>
                </nav>
            </header>
            <main className={classes.Content}>
                {props.children}
            </main>
            </>
        );
}

export default Layout;