import React from "react";
import classes from "./Icon.module.css";
import PkmnNames from "../../Assets/Strings/Pokemon";

const fullpath = "https://raw.githubusercontent.com/Leanny/leanny.github.io/master/seedchecker/img/icons/";
const Icon = props => {
    let filename = "b_" + props.details.Species;
    if(props.details.AltForm > 0) {
        filename += "-" + props.details.AltForm;
    }
    let wrapperClass = [classes.Img];
    if(props.details.IsGigantamax) {
        wrapperClass.push(classes.Gmax);
    }
    if(props.details.ShinyForced) {
        if(props.details.ShinyForced === 1) {
            wrapperClass.push(classes.NoShiny);
        } else {
            wrapperClass.push(classes.Shiny);
        }
    }
    return <div className={wrapperClass.join(" ")}><img src={fullpath+filename+".png"} alt={PkmnNames[props.details.Species]} title={PkmnNames[props.details.Species]} />{PkmnNames[props.details.Species]}</div>;
}

export default Icon;